<script>
  export default {
    name: 'notes',
    data() {
      return {
        password: 'Password',
        show: false,
        message: '',
        marker: true,
        iconIndex: 0,
        icons: [
          'mdi-emoticon',
          'mdi-emoticon-cool',
          'mdi-emoticon-dead',
          'mdi-emoticon-excited',
          'mdi-emoticon-happy',
          'mdi-emoticon-neutral',
          'mdi-emoticon-sad',
          'mdi-emoticon-tongue',
        ],
      }
    },

    computed: {
      icon() {
        return this.icons[this.iconIndex]
      },
    },

    methods: {
      toggleMarker() {
        this.marker = !this.marker
      },
      sendMessage() {
        this.resetIcon()
        this.clearMessage()
      },
      clearMessage() {
        this.message = ''
      },
      resetIcon() {
        this.iconIndex = 0
      },
      changeIcon() {
        this.iconIndex === this.icons.length - 1 ?
          this.iconIndex = 0 :
          this.iconIndex++
      },
    },

  }
</script>